/*============================================================================
	#Home Page
==============================================================================*/
.store-front {
	padding-top: 24px;
	margin-bottom: 5px;

	&__content {
		width: calc(100% - 290px - 40px);
		margin-left: auto;
	}
}

.main-slider {

	.swiper-slide {
		height: 400px;
		overflow: hidden;
		border-radius: 6px;
	}
	
	.swiper-button-prev,
	.swiper-button-next {
		transform: rotate(180deg);
		color: $orange;
		top: calc( 50% - 27px);

		&:after {
			font-size: 34px;
		}

		&::before {
			position: absolute;
			content: '';
			height: 48px;
			width: 48px;
			border-radius: 8px;
			background: #f1f1f1;
			z-index: -1;
			transform: rotate(45deg);

		}
	} 

	.swiper-button-prev {
		left: -4px;

		&::before {
			left: 2px;
		}
	}

	.swiper-button-next {
		right: -4px;

		&::before {
			right: 2px;
		}
	}

	.swiper-pagination {
		display: flex;
		justify-content: center;
		position: static;
		margin-top: 4px ;
	}

	.swiper-pagination-bullet {
		width: auto;
		flex: 1 1 auto;
		border-radius: 6px;
		height: 4px;
		margin-left: 0 !important;
		margin-right: 8px !important;
	}

	.swiper-pagination-bullet:last-child {
		margin-right: 0 !important;
	}

	.swiper-pagination-bullet-active {
		background: $orange;
	}
}

.brands-slider {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 20px 0;

	&__content {
		width: 80%;
		padding: 0 40px;
		position: relative;
	}

	&__title {
		font-size: 1.8rem;
		font-weight: $semiBold;
	}

	.swiper-slide {
		height: 40px;
		
		img {
			height: 100%;
			width: 100%;
			object-fit: contain;
		}
	}

	.swiper-button-prev,
	.swiper-button-next {
		&::after {
			font-size: 20px;
			color: $orange;
		}
	}

	.swiper-button-prev {
		left: 0 !important;
		transform: rotate(180deg);
	}

	.swiper-button-next {
		right: 0 !important;
		transform: rotate(180deg);
	}
}

