/*============================================================================
	#Typography
==============================================================================*/
html {
	font-size: 62.5%;
}

body,
button,
input,
select,
optgroup,
textarea {
	color: #333;
	font-family: 'Open Sans', sans-serif;;
	font-size: 1.4rem;
    line-height: 1.6;
    font-weight: $regular;
	// letter-spacing: 0.025em;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
	-webkit-text-size-adjust: 100%;
}