/*============================================================================
	#Footer
==============================================================================*/
.footer {
	color: white;
	background: $darkGray;
	padding: 5.2rem 0 5rem;
	letter-spacing: -0.02em;

	&__row {
		display: flex;
		margin: 0 -0.5rem

	}

	&__col {
		padding: 0 4.5rem;
		flex: 1 1 0%;
	}

	&__logo {
		margin-bottom: 4rem !important;
	}

	.link {
		color: white;

		&:active,
		&:visited {
			color: white;
		}

		&:hover {
			color: $blue;
		}
	}
	
}

.footer-widget {
	

	&__title {
		margin: 0 0 1rem 0;
		padding: 0 0 1.4rem 0;
		font-size: 1.6rem;
		
		font-weight: $semiBold;
		border-bottom: 4px solid #025fc8;
	}

	&__list {
		padding: 0;
		margin: 0;
		list-style-type: none;
	}

	&__item {
		margin-bottom: 0.3rem;
	}

	.messanger-link {
		margin-top: 0.4rem;
		margin-right: 0.6rem;
		display: inline-block;
		transition: opacity .3s;

		&:hover {
			opacity: 0.7;
		}
	}
}

.footer-logo {

	margin: 0 auto;
	text-align: center;

	&__image {
		margin-bottom: 4px;
	}

	&__name {
		font-size: 1.7rem;
	}
}

.site-security {
	display: flex;
	justify-content: space-between;
	margin-top: 5.5rem;
}

.social-media {
	text-align: center;
	
	&__title {
		font-size: 1.6rem;
		margin-bottom: 1rem;
	}

	&__list {
		padding: 0;
		margin: 0;
		list-style-type: none;
		display: flex;
		justify-content: center;
		
	}

	&__item {
		margin: 0 0.5rem;
	}

	&__link {
		display: block;
		transition: opacity 0.3s;

		img {
			width: 36px;
			height: 36px;
		}

		&:hover {
			opacity: 0.7;
		}
	}
}