/*============================================================================
	#Header
==============================================================================*/
.header {
	position: relative;
	z-index: 103;

	&__top {
		background: #3b3b3b;
		padding: 2.2rem 0 1.4rem;

		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}

	&__bottom {
		background: rgb(2,95,201);
		background: linear-gradient(90deg, rgba(1, 95, 201,1) 0%, rgba(214, 94, 74,1) 80%);

		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}

	&__logo {
		display: block;
		padding:0 4.2rem;
	}

	&__contacts {
		display: flex;
		flex-direction: column;
		align-items: center;
		line-height: 1.2;
		margin-bottom: 0.6rem;
	}

	&__phones {
		color: #fff;

		.link {
			color: #fff;
			font-weight: 600;

			&:hover {
				color: $orange;
			}
		}
	}

	&__worktime {
		color: white;
	}
}

.header-nav {

	&__list {
		display: flex;
		justify-content: space-between;
	}

	&__item {
		color: white;
		font-size: 1.5rem;
		font-weight: $bold;
		margin: 0 1.1rem;
		margin-bottom: 0.8rem;
	}
}

.site-lng {

	&__button {
		border: 0.2rem solid white;
		border-radius: 0.8rem;
		font-size: 1.8rem;
		color: white;
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0.4rem 1rem 0.4rem 1.2rem;
		margin-bottom: 0.8rem;
		cursor: pointer;
		transition: background 0.2s;

		&:hover {
			background: $orange;
		}
	}

	.icon-arrow-down {
		fill: white;
		width: 1.4rem;
		height: 1.6rem;
		margin-top: 0.2rem;
		margin-left: 1rem;
		transform: rotate(90deg);
	}
}

.menu-button {
	flex: 0 0 290px;
	max-width: 290px;
	position: relative;
	
	&__button {
		display: flex;
		align-items: center;
		background: white;
		border-radius: 8px;
		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;
		padding: 2rem;
		font-size: 16px;
		width: 100%;
		line-height: 1.4;
		cursor: pointer;
		transition-delay: 0s;
		// transition: border-radius 0.3s;
	}

	&__icon {
		display: flex;
		flex-direction: column;
		width: 3.7rem;
		margin-right: 2.8rem;

		span {
			display: inline-block;
			height: 2px;
			margin: 2.5px 0;
			width: 100%;
			background-color: #3b3b3b;
			border-radius: 6px;
		}
	}

	&__text {
		font-weight: $semiBold;
		color: black;
	}

}

.header-srch {
	padding: 0 30px 0 40px;
	flex: 1 0 auto;
	
	&__input {
		width: 100%;
		border: none;
		border-radius: 6px;
		padding: 5px 10px;
		font-size: 1.4rem;

		&::placeholder {
			color: #818181;
		}
		
		&:focus::placeholder {
			opacity: 0;
		}
	}
}

.header-controll {
	display: flex;
	justify-content: space-between;

	&__button {
		color: white;
		font-size: 0.8rem;
		text-transform: uppercase;
		letter-spacing: 0.2em;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		margin: 0 1.2rem;
		transition: color 0.2s;
		font-weight: $bold;

		cursor: pointer;

		&:hover {
			// color: $blueHover;
			svg {
				fill: $blueHover;
			}
		}
	}

	.icon-heart,
	.icon-balance,
	.icon-cart {
		fill: white;
		width: 2.4rem;
		height: 2.4rem;
		margin-bottom: 4px;
		margin-top: 4px;
		transition: fill 0.2s;
	}
}

.menu {
	position: absolute;
	width: 100%;
	top: 62px;
	background: white;
	border-bottom-left-radius: 8px;
	border-bottom-right-radius: 8px;

	&__list {
		margin: 0;
		padding: 1.8rem 0rem 2.4rem 2rem;;
		list-style-type: none;

	}

	&__item {
		margin-bottom: 1.1rem;
		padding-right: 1rem;

		&:last-child {
			margin-bottom: 0;
		}

		&:hover {
			.menu__link {
				color: $blue;
			}

			.icon-arrow-prev {
				fill: $blue;
			}
		}
	}

	&__link {
		font-weight: 600;
		position: relative;
		display: block;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		padding-right: 1.4rem;
		padding-left: 30px;
		z-index: 1;

		.icon-arrow-prev {
			position: absolute;
			width: 1.4rem;
			height: 1.4rem;
			fill: $orange;
			right: 0px;
			top: calc(50% - 7px);
			transition: fill 0.3s;
		}


		&._sale {
			padding-left: 3rem;

			&::before {
				content: '';
				position: absolute;
				background: url('../icons/icon-coupon-blue.svg');
				width: 2rem;
				height: 2rem;
				left: 0;
				top: 1px;
			}
		}

		&:active,
		&:visited {
			color: #333333;
		}
	}

	&__link-icon {
		width: 20px;
		height: 20px;
		position: absolute;
		left: 0;
		top: calc(50% - 10px);

	}
	
}

.menu-button._is-hidden {
	
	.menu-button__button {
		border-radius: 8px;
		transition-delay: 0.3s;
		transition: border-radius 0.3s;

	}
}

.submenu {
	background: white;
	position: absolute;
	top: 0;
	left: 100%;
	transform: translateX(30px);
	opacity: 0;
	visibility: hidden;
	pointer-events: none;
	transition: all 0.3s;

	// width: calc((240px * 3) + 40px);
	min-height: 100%;
	padding:1.4rem 2rem 2.4rem 3.6rem;

	&__row {
		display: flex;
		flex-wrap: nowrap;
	}

	&__col {
		flex: 0 0 240px;
		max-width: 240px;
		margin-right: 10px;
	}

	&__title {
		color: $blue;
		margin: 0 0 1rem 0;
		display: inline-block;
		font-size: 1.6rem;
		font-weight: $bold;
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}

	&__list {
		padding: 0;
		margin: 0;
		list-style-type: none;
	}

	&__list-item {
		margin-bottom: 1.1rem;
	}

	&__link {
		font-weight: $semiBold;
	}
}

.submenu {

	&._visible {

		transform: translateX(00px);
		opacity: 1;
		visibility: visible;
		pointer-events: all;
	}
}