/*============================================================================
	#Common
==============================================================================*/
.site {

	.container {
		max-width: 1400px;
		width: 100%;
		padding: 0 20px;
		margin: 0 auto;
	}

	&::after {
		background: rgba(0,0,0,0.6);
		position: fixed;
		top: 0;
		bottom: 0;
		content: '';
		left: 0;
		right: 0;
		transition: all 400ms ease-in-out;
		opacity: 0;
		visibility: hidden;
		z-index: 102;
	}

}

body._is-hover-menu .site::after {
	opacity: 1;
	visibility: visible;
}

.link {
	text-decoration: none;
	color: #333;
	transition: 0.2s;

	&:hover {
		color: $blue;
	}
}

.section-title {
	color: #015fc9;
	font-weight: $bold;
	font-size: 30px;
	margin: 0 0 4px 0;
	letter-spacing: -0.02em; 
 }

.items {

	&__grid {
		margin: 0 -0.5rem;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		&._six {

			.items__col {
				flex: 0 0 16.666%;
				max-width: 16.666%;
				position: relative;
			}
		}
	}

	&__col {
		padding: 5px;
	}
}

.item-card {
	background: white;
	border-radius: 12px;
	padding: 6px 16px;
	box-shadow: 0 0 20px 2px rgba(0,0,0,0.1);

	&__header {
		display: flex;
		display: none;
		align-items: center;
		margin-bottom: 14px;

		.button {
			fill: #7f7f7f;
			margin-right: 10px;
		}

		.icon-youtube {
			width: 26px;
			height: 26px;
		}

		.icon-360-degrees {
			width: 33px;
			height: 33px;
			margin-top: -4px;
		}

	}

	&__code {
		font-size: 8.5px;
		margin-left: auto;
		color: #ccc9c9;
	}

	&__media {
		margin: 1rem 0 0.4rem 0;
		padding: 0;
	}

	&__link {
		display: block;
		display: -webkit-box;
		font-size: 14.5px;
		font-weight: 600;
		line-height: 1.25;
		margin-bottom: 0px;
		height: 54px;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;  
		overflow: hidden;
	}

	&__in-stock {
		color: #9aee62;
		font-weight: 600;
		font-size: 11.5px;
	}

	.item-card-price {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		font-weight: 600;
		margin-bottom: 4px;

		&__discount {
			background: #f96951;
			color: white;
			padding: 0px 8px;
			border-radius: 6px;
			margin-right: 10px;
			font-size: 12px;
		}

		&__old {
			color: #999999;
			margin-right: 10px;
			text-decoration: line-through;
			font-size: 12px;
		}

		&__current {
			font-size: 20px;
			font-weight: $bold;
			line-height: 1;

			span {
				font-size: 13px;
				font-weight: $bold;
			}
		}

	}

	&__button {
		background: $orange;
		color: white;
		display: block;
		width: 100%;
		border-radius: 12px;
		padding: 8px 16px;
		text-align: left;
		display: flex;
		align-items: center;
		cursor: pointer;
		margin-bottom: 10px;
		line-height: 1;
		transition: background 0.3s;
		
		._text {
			margin: 0 auto;
			font-size: 1.4rem;
			font-weight: $bold;

		}

		.icon-cart {
			width: 2rem;
			fill: white;
			height: 2rem;
			fill: white;
			flex: 0 0 auto;

		}

		&:hover {
			background: $blue;
		}
	}

	&__footer {
		display: none;
	}

	&__group {
		margin-bottom: 6px;
	}

	&__opt-name {
		font-weight: 600;
		margin: 0 0 8px 0;
		font-size: 11px;
	}

	&__opt-list {
		input[type="radio"] {
			position: absolute;
			visibility: hidden;
			opacity: 0;
		}

		label {
			width: 22px;
			height: 22px;
			border-radius: 50%;
			display: inline-block;
			margin-right: 7px;
			cursor: pointer;

			&:last-child {
				margin-right: 0;
			}
		}
	}

	&__props {
		padding: 0;
		margin: 0;
		list-style-type: none;
		margin-top: -4px;
		font-size: 11px;
		color: #b9b5b5;
		// line-height: 1.2;
	}
}

.item-card:hover {
	position: absolute;
	width: calc(100% - 10px);
	left: 5px;
	z-index: 10;
	margin-top: -30px;
	.item-card__header {
		display: flex;
	}

	.item-card__footer {
		display: block;
	}
}

.section-items {
	position: relative;
	margin-bottom: 20px;

	&::after {
		content: '';
		height: 0;
		width: 100%;
		bottom: 0;
		position: absolute;
		z-index: 1;
		transition: height .3s;
		background: linear-gradient(to bottom, rgba(243,243,243,0) 0%,rgba(243,243,243,1) 85%,rgba(241,241,241,1) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#f1f1f1',GradientType=0 );
	}

	&__content {
		position: relative;
		padding-bottom: 50px;
		
		&::before {	
			content: '';
			height: 2px;
			width: 100%;
			background: #c1d4e9;
			bottom: 0;
			position: absolute;
			z-index: 2;
		}
	}

	&__toggle {
		border-radius: 100%;
		width: 50px;
		height: 50px;
		background: #015fc9;
		position: absolute;
		left: calc(50% - 25px);
		display: flex;
		justify-content: center;
		align-items: center;
		box-shadow: 0 0 18px 8px rgba(1, 94, 201, 0.4);
		cursor: pointer;
		margin-top: -25px;
		z-index: 10;
		transition: transform .3s;

		.icon-chevron-up {
			fill: white;
			width: 20px;
			height: 20px;
			transition: transform .3s;
			transform: rotate(0deg);
		}

		&:hover {
			transform: scale3d(1.1,1.1,1.1);
		}

	}
}

.js-section-collapse._hidden {

	&::after {
		height: 160px;
	}
	
	.container {
		overflow: hidden;
	}

	.section-items__toggle {

		.icon-chevron-up {
			transform: rotate(180deg);
		}
	}
	
}

.js-section-collapse .section-items__content {
	transition: all 0.4s;
}



